section.projectContainer {
    width: 100%;
    padding: 90px 0px;
    background: linear-gradient(85deg, rgba(255, 245, 0, 0.70) 0.24%, rgba(118, 255, 173, 0.57) 54.7%, rgba(80, 141, 233, 0.70) 97.96%);
}
section.projectContainer div.projectContent {
    margin:  auto;
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

section.projectContainer div.projectContent  div.projectTitleContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;

}

section.projectContainer div.projectContent div.projectTitleContainer img {
    width: 55px;
    height: 55px;
    margin: 0px 40px;
   
}
section.projectContainer div.projectContent div.projectTitleContainer span {
    color: #FFF;
    font-family: Poppins;
    font-size: 55px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}
section.projectContainer div.projectContent div.projectCarrousel {
    display: grid;
    grid-auto-flow: column;
    scroll-behavior: auto;
    gap: 1.5rem;
    overflow-y: auto;
    overscroll-behavior-x: contain;
    scroll-snap-type: x mandatory;
    scrollbar-width: none;

}
div.projectCarrouselContent {
    max-width: 1300px;

    
}

div.projectCarrousel::-webkit-scrollbar {
    display: none;

}
section.projectContainer div.projectContent div.projectView {
    margin-top: 50px;
    border-radius: 30px;
    border: 2px solid #FFF;
    background: linear-gradient(71deg, rgba(255, 255, 255, 0.39) 0.48%, rgba(212, 212, 212, 0.39) 99.4%);
    backdrop-filter: blur(40px);
    padding: 50px;
    display: flex;
    gap: 80px;
    align-items: center;
    justify-content: center;
}

div.projectView div.projectCard div.projectBannerContainer  {
    display: flex;
    flex-direction: column;
}

div.projectView div.projectCard {
    padding: 30px;
    background-color: #071655;
    width: 493px;
    display: flex;
    flex-direction: column;
    border-radius: 20px;
    height: 556px;

}

div.projectView div.projectCard div.projectBannerContainer img {
    height: auto;
    border: 1px solid #fff;
    border-radius: 20px;
}



div.projectView div.projectCard div.projectBannerContainer span.projectDetails {
    color: #E1E1E1;
    font-family: Poppins;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    padding: 10px 20px ;

}

div.projectView div.projectCard {
    display: flex;
    justify-content: space-between;
}
div.projectView div.projectCard  a {
    display: flex;
    justify-content: center;
    text-decoration: none;
}

div.projectView div.projectCard button {
    width: 100%;
    background: linear-gradient(88deg, #F2B705 0%, rgba(118, 255, 173, 0.81) 54.33%, #508DE9 99.34%);
    border: 0px;
    padding: 15px 0px;
    border-radius: 20px;
    margin: 0px 20px;

}

div.projectView div.projectCard button span{
    color: #FFF;
    font-family: Poppins;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
}
div.projectDetailsTitle {
    display: flex;
    flex-direction: column;
}
div.projectDetailsContent {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
   
    gap:20px
}

span.projectDetailsTitleText {
    color: #FFF;
    font-family: Poppins;
    font-size: 55px;
    font-style: normal;
    font-weight: 700;
}

span.projectDetailsTitleDescription {
    color: #FFF;
    text-align: justify;
    font-family: Poppins;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
}

div.projectDetailsTechs span, div.projectDetailsSetup span {
    color: #FFF;
    text-align: justify;
    font-family: Poppins;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
}

.iconTechDetails {
    height: 43px;
    width: 43px;
    margin-right: 10px;
    margin-top: 10px;
}

div.projectDetailsSetup  ul{
    padding-left: 20px;
}
div.projectDetailsSetup  ul li {
    color: #fff;
    font-family: Poppins;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
}

.swiper-button-next, .swiper-button-prev {
    color: #fff !important; 
}
.swiper-pagination-bullet {
    width: 15px !important;
    height: 15px !important;
    background-color: #fff !important; 
}

@media screen and (max-width:1300px) {
    div.projectCarrouselContent {
        max-width: 120%;
    }
    section.projectContainer div.projectContent div.projectView {
        height: auto;
        gap: 30px;
        justify-content: center;
    }
    div.projectView div.projectCard {
        width: 300px;
        height: 370px;
    }
    div.projectView div.projectCard button {
        padding: 10px 0px;
        margin: 0px 10px;
    }
    div.projectView div.projectCard button span {
        font-size: 16px;

    }
    span.projectDetailsTitleText {
        font-size: 35px;
    }
    span.projectDetailsTitleDescription,div.projectDetailsSetup  ul li {
        font-size: 15px;

    }
    div.projectView div.projectCard div.projectBannerContainer span.projectDetails {
        font-size: 10px;

    }
    div.projectDetailsContent {
        padding: 20px 0px;
    }
    .iconTechDetails {
        height: 35px;
        width: 35px;
    }
    div.projectDetailsTechs span, div.projectDetailsSetup span {
        font-size: 17px;
    }

}

@media screen and (max-width:855px) {
    section.projectContainer div.projectContent div.projectTitleContainer span {
        font-size: 35px;
    }
    section.projectContainer div.projectContent div.projectTitleContainer img {
        height: 40px;
        width: 40px;
        margin-right: 30px;
        margin-left: 0px;
    }

    span.projectDetailsTitleText {
        font-size: 25px;
    }
    span.projectDetailsTitleDescription,div.projectDetailsSetup  ul li {
        font-size: 12px;

    }
    div.projectDetailsTechs span, div.projectDetailsSetup span {
        font-size: 14px;
    }
    .iconTechDetails {
        height: 25px;
        width: 25px;

    }
    div.projectDetailsContent {
        gap:20px

    }
    div.projectView div.projectCard{
        width: 250px
    }

}

@media screen and (max-width:667px) {
    section.projectContainer {
        padding: 50px 0px;
    }
    section.projectContainer div.projectContent div.projectView {
        flex-direction: column;
        padding: 45px;
    }
    div.projectView div.projectCard{
        width: 100%;
        height: auto;
        
    }
    div.projectDetailsContent {
        width: 100%;
        gap:20px

    }
    div.swiper-button-prev,  div.swiper-button-next{
        scale: 0.7 !important;
    }
}
@media screen and (max-width:482px) {
    section.projectContainer {
        padding: 30px 0px;
    }
    
    section.projectContainer div.projectContent div.projectView {
        gap: 10px;
        padding: 30px;
        margin-top: 20px;

    }
    section.projectContainer div.projectContent div.projectTitleContainer span {
        font-size: 25px;
    }
    section.projectContainer div.projectContent div.projectTitleContainer img {
        height: 30px;
        width:  30px;
        margin-right: 20px;

    }
    div.projectView div.projectCard {
        padding: 15px;
    }
    div.projectView div.projectCard button {
        padding: 7px 0px;
        border-radius: 10px;


    }
    div.projectView div.projectCard button span {
        font-size: 10px;
    }
    div.projectView div.projectCard div.projectBannerContainer span.projectDetails {
        font-size: 8px;
        padding: 10px ;
    }
    div.projectDetailsTechs span, div.projectDetailsSetup span {
        font-size: 12px;
    }
    span.projectDetailsTitleDescription,div.projectDetailsSetup  ul li {
        font-size: 10px;

    }
    span.projectDetailsTitleText {
        font-size: 18px;
    }
    .iconTechDetails {
        height: 20px;
        width: 20px;

    }
    div.projectDetailsContent {
        gap:10px

    }
    section.projectContainer div.projectContent div.projectTitleContainer span {
        font-size: 20px;
    }
    section.projectContainer div.projectContent div.projectTitleContainer img {
        height: 20px;
        width: 20px;
    }
    div.swiper-button-prev,  div.swiper-button-next{
        scale: 0.5 !important;
    }


}

