section.careerContainer {
    width: 100%;
    /* height: 100vh; */
    padding: 0px 30px;

}
div.careerBox {
    max-width: 1454px;
    margin: auto;
    border: 2px solid #544A4A;
    background: rgba(96, 96, 96, 0.19);
    backdrop-filter: blur(35px);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border-top: 0px;
    border-bottom: 0px;
    padding: 100px 0px;
}

div.careerTitle {
    display: flex;
    align-items: center;
}

div.careerTitle span {
    font-family: Poppins;
    font-size: 55px;
    font-style: normal;
    font-weight: 700;
    background: linear-gradient(88deg, #FFF500 0%, rgba(118, 255, 173, 0.81) 72.95%, #508DE9 99.34%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    padding-left: 40px;
}

div.careerTitle img {
    width: 56px;
    height: 56px;
}

div.careerDetails {
    margin-top: 70px;
}

div.companyDetails {
    
    display: flex;
    flex-direction: column;
    align-items: center;
}

div.companyDetails span:first-child {
    color: #FFF;
    font-family: Poppins;
    font-size: 25px;
    font-style: normal;
    font-weight: 700;
}
div.companyDetails span:last-child {
    color: #737373;
    font-family: Poppins;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
}




div.careerDetails {
    display: flex;
    width: 70%;
    justify-content: space-between; 

}

div.lineCareerOffice {
    height: 5px;
    width: 100%;
    background: linear-gradient(88deg, #FFF500 0%, rgba(118, 255, 173, 0.81) 72.95%, #508DE9 99.34%);
    margin-bottom: 40px;
}

div.careerSeparator {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

div.careerIconContainer {
   height: 70px;
   width: 70px;
   display: flex;
   align-items: center;
   justify-content: center;
   background: linear-gradient(88deg, #FFF500 0%, rgba(118, 255, 173, 0.81) 72.95%, #508DE9 99.34%);
    border-radius: 50%;
}

div.careerIconContainer img {
    width: 38px;
    height: 38px;
}

div.careerLine {
    flex: 1;
    width: 10px;
    background: linear-gradient(88deg, #FFF500 0%, rgba(118, 255, 173, 0.81) 72.95%, #508DE9 99.34%);
    margin: 0 auto;
}

div.careerBubble {
    width: 30px;
    height: 30px;
    border-radius: 25px;
    background: linear-gradient(88deg, #FFF500 0%, rgba(118, 255, 173, 0.81) 72.95%, #508DE9 99.34%);
    margin: 0 auto;
}
div.careerOfficeTitleContent {
    display: flex;
    flex-direction: column;

}

div.careerOfficeTitleContent span {
    color: #fff;
    font-family: Poppins;
    font-size: 25px;
    font-style: normal;
    font-weight: 700;
    margin-bottom: 20px;
}
div.careerOfficeActivityContainer {
    display: flex;
    flex-direction: column  ;
    gap: 25px;
    width: 100%;

}
div.careerOfficeActivity span {
    color: #FFF;
    font-family: Poppins;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    
}
@media screen and (max-width:1200px) {
    div.careerDetails {
        width: 75%;
    }
    div.careerTitle span{   
        font-size: 40px;
    }
    div.careerTitle img {
        height: 40px;
        width: 40px;
    }
    div.companyDetails span:first-child {
        font-size: 20px;
    }
    div.companyDetails span:last-child {
        font-size: 14px;
    }
    div.careerIconContainer {
        height: 60px;
        width: 60px;
       
    }
    div.careerIconContainer img {
        height: 30px;
        width: 30px;
    }
    div.careerOfficeTitleContent span {
        font-size: 28px;
        margin-bottom: 15px;
    }
    div.lineCareerOffice {
        height: 4px;
        margin-bottom: 30px;
    }
    div.careerOfficeActivity span {
        font-size: 18px;
    }

}

@media screen and (max-width:1000px) {
    div.careerDetails {
        width: 85%;
    }
}
@media screen and (max-width:847px) {
    div.careerTitle span{   
        font-size: 30px;
    }
    div.careerTitle img {
        height: 35px;
        width: 35px;
    }
    div.careerTitle span {
        padding-left: 20px;
    }
    div.companyDetails span:first-child {
        font-size: 18px;
    }
    div.companyDetails span:last-child {
        font-size: 12px;
    }
    div.careerIconContainer {
        height: 50px;
        width: 50px;
    }
    div.careerIconContainer img {
        height: 25px;
        width: 25px;
    }
    div.careerOfficeTitleContent span {
        font-size: 20px;
        margin-bottom: 10px;
    }
    div.careerOfficeActivity span {
        font-size: 14px;
    }
}
@media screen and (max-width:707px) {
    div.careerTitle span{   
        font-size: 22px;
    }
    div.careerTitle img {
        height: 25px;
        width: 25px;
    }
    div.careerIconContainer {
        height: 40px;
        width: 40px;
       
    }
    div.careerIconContainer img {
        height: 20px;
        width: 20px;
    }
    div.careerLine {
        width: 5px;
    }
    div.careerBubble {
        width: 20px;
        height: 20px;
    }
    div.lineCareerOffice {
        margin-bottom: 20px;
    }

    div.companyDetails span:first-child {
        font-size: 14px;
    }
    div.companyDetails span:last-child {
        font-size: 8px;
    }
    div.careerOfficeTitleContent span {
        font-size: 16px;
        margin-bottom: 10px;
    }
    div.careerOfficeActivity span {
        font-size: 10px;
    }
    div.careerOfficeActivityContainer {
        gap: 15px
    
    }
}

@media screen and (max-width:529px) {
    div.careerBox {
        padding: 50px 0px;
    }
    div.careerDetails   {
        margin-top: 30px;
    }

    div.careerBox div.careerTitle span {   
        font-size: 14px;
        padding-left: 10px;
    }
    div.careerBox div.careerTitle img{   
        height: 15px;
        width: 15px;
    }
    div.careerDetails {
        width: 85%;
    }
    div.careerIconContainer {
        height: 25px;
        width: 25px;
    }
    div.careerIconContainer img {
        height: 10px;
        width: 10px;
    }
    div.careerBubble {
        width: 10px;
        height: 10px;
    }
    div.careerLine {
        width: 3px;
    }
    div.companyDetails span:first-child {
        font-size: 10px;
    }
    div.companyDetails span:last-child {
        font-size: 6px;
    }
    div.careerTitle span{   
        font-size: 20px;
    }
    div.careerOfficeTitleContent span {
        font-size: 10px;
        margin-bottom: 5px;
    }
    div.lineCareerOffice {
        height: 2px;
        margin-bottom: 5px;
        

    }
    div.careerOfficeActivity span {
        font-size: 8px;
    }
    div.careerOfficeActivityContainer {
        gap: 2px;
    }
    div.careerOfficeContainer {
        max-width: 50%;
    }

}
