section.contactContainer {
    width: 100%;
    padding: 0px 30px;


}

div.contactBox {
    position: relative;
    max-width: 1454px;
    margin: auto;
    background: rgba(96, 96, 96, 0.19);
    backdrop-filter: blur(10px);
    border: 2px   solid #544A4A;
    border-top: 0px;
    border-bottom: 0px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 100px;
    top: -1px;
}
div.contactTitleContainer {
    display: flex;
    width: 70%;
    align-items: center;
    margin-bottom: 20px;
    
}
div.contactTitleContainer img{
    height: 50px;
    width: 50px;
}
div.contactTitleContainer span{
    font-family: 'Poppins';
    font-size: 55px;
    font-style: normal;
    font-weight: 700;
    background: linear-gradient(88deg, #FFF500 0%, rgba(118, 255, 173, 0.81) 72.95%, #508DE9 99.34%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    padding-left: 40px;
}

div.contactDetails {
    display: flex;
    gap: 100px
}

div.socialIconContainer a img {
    width: 44px;
    height: 44px;
}

div.formContainer {
    display: flex;
    flex-direction: column;
    border-radius: 30px;
    border: 3px solid rgba(189, 189, 189, 0.19);
    background: rgba(17, 21, 35, 0.37);
    backdrop-filter: blur(35px);
    width: 683px;
    padding: 65px;
    gap: 22px

} 

div.inputContainer {
    display: flex ;
    flex-direction: column;
}
div.textareaContainer {
    display: flex ;
    flex-direction: column;
}
div.inputContainer input {

    padding: 20px;
    background-color: transparent ;
    border-color: #D6D6D6;
    border-width: 3px;
    border-radius: 20px;
}

div.textareaContainer textarea {
    padding: 20px;
    height: 213px;
    background-color: transparent ;
    border-color: #D6D6D6;
    border-width: 3px;
    border-radius: 20px;
    outline: none;
}

div.contactDetails button {
    padding: 25px 0px;
    border: 0;
    border-radius: 20px;
    background: linear-gradient(85deg, rgba(255, 245, 0, 0.70) 0.24%, rgba(118, 255, 173, 0.57) 54.7%, rgba(80, 141, 233, 0.70) 97.96%);
}
div.contactDetails button span {
    color: #232426;
    font-family: Poppins;
    font-size: 19px;
    font-style: normal;
    font-weight: 700;
}

div.inputContainer span, div.textareaContainer span {
    color: #FFF;
    font-family: Poppins;
    font-size: 22px;
    font-style: normal;
    font-weight: 700;
    margin-bottom: 8px;
}
div.inputContainer input::placeholder,  div.textareaContainer textarea::placeholder{
    color: #545454;
    font-family: Poppins;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    margin-bottom: 8px;
}

div.emailTitleContainer, div.socialContainer{
    display: flex;
    align-items: center;
    gap: 20px;
}
div.emailTitleContainer img, div.socialContainer img {
    width: 24px;
    height: 24px;
}

div.emailTitleContainer span, div.socialContainer span {
    color: #FFF;
    font-family: Poppins;
    font-size: 22px;
    font-style: normal;
    font-weight: 700;
}

div.moreInformationLine {
    width: 100%;
    height: 4px;
    background: linear-gradient(90deg, #FFF500 0%, rgba(118, 255, 173, 0.81) 46.5%, #508DE9 99.21%);
    margin: 20px 0px;
}
div.myEmailContainer span.textEmail {
    color: #9F9F9F;
    font-family: Poppins;
    font-size: 22px;
    font-style: normal;
    font-weight: 700;
}

div.mySocialContainer {
    margin-top: 40px ;
}

div.socialIconContainer {
    display: flex ;
    gap: 20px
}

@media screen and (max-width:1257px) {
    div.contactTitleContainer {
        width: 80%;
    }
    div.formContainer {
        width: 500px;
        padding: 35px;
        gap: 15px;
    }
    div.textareaContainer textarea {
        height: 150px;
    }
    div.contactDetails button {
        padding: 20px;
    }
    div.contactDetails {
        gap: 50px;
    }
    
}
@media screen and (max-width:1016px) {
    div.formContainer {
        width: 350px;
        padding: 35px;
        gap: 15px;
        border-radius: 20px;

    }
    div.textareaContainer textarea {
        height: 100px;
        border-radius: 10px;

    }
    div.inputContainer span, div.textareaContainer span {
        font-size: 16px;
    }
    div.inputContainer input {
        padding: 10px;
        border-radius: 10px;
    }
    div.inputContainer input::placeholder,  div.textareaContainer textarea::placeholder {
        font-size: 14px;

    }
    div.contactDetails button {
        padding: 10px 0px;
        border-radius: 10px;

    }
    div.contactDetails button span {
        font-size: 14px;

    }
    div.contactTitleContainer span {
        font-size: 40px;
        padding-left: 20px;

    }
    div.contactTitleContainer img {
        height: 30px;
        width: 30px;

    }
    div.emailTitleContainer img, div.socialContainer img {
        width: 18px;
        height: 18px;
    }
    div.emailTitleContainer span, div.socialContainer span {
        font-size: 16px;
    }
    div.myEmailContainer span.textEmail {
        font-size: 16px;

    }
    div.socialIconContainer a img {
        width: 30px;
        height: 30px;
    }

}

@media screen and (max-width:767px) {
    div.contactDetails {
        flex-direction: column;
        width: 100%;
        padding: 30px;
    }
    div.formContainer {
        width: 100%;
    }

}
@media screen and (max-width:404px) {
    div.contactTitleContainer span {
        font-size: 30px;
        padding-left: 10px ;
    }
    div.contactTitleContainer img {
        height: 30px;
        width: 30px;
    }
    div.formContainer {
        padding: 20px;
    }
    div.inputContainer input::placeholder,  div.textareaContainer textarea::placeholder {
        font-size: 10px;
    }
    div.textareaContainer textarea {
        height: 80px;
    
    }
    div.inputContainer span, div.textareaContainer span {
        font-size: 12px;

    }
    div.contactDetails button span {
        font-size: 10px;

    }
    div.emailTitleContainer span, div.socialContainer span {
        font-size: 12px;
    }
    div.emailTitleContainer img, div.socialContainer img {
        width: 14px;
        height: 14px;
    }
    div.socialIconContainer a img {
        width: 20px;
        height: 20px;
    }
    div.contactBox {
        padding-bottom: 50px;
    }
    div.myEmailContainer span.textEmail {
        font-size: 12px;
    }

}