section.profileContainer {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 0px 30px;
}
section.profileContainer div.profileBox div.profileBoxContainer {
    display: flex;
    gap: 80px

    
}
#linkCurriculo {
    width: 100%;
}

div.profileBox {
    border-radius: 24px 24px 0px 0px ;
    max-width: 1454px;
    margin: auto;
    padding: 88px 94px 48px 94px;
    background: rgba(96, 96, 96, 0.19); 
    backdrop-filter: blur(10px);
    border: 2px   solid #544A4A;
    border-bottom: 0px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

div.aboutMeContent {
    max-width: 591px;

}
div.aboutMeContent div.profileTitle {
    width: 100%;
    
}

div.aboutMeContent span.profileTitleText {
    font-family: Poppins;
    font-size: 45px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    background: linear-gradient(88deg, #FFF500 0%, rgba(118, 255, 173, 0.81) 72.95%, #508DE9 99.34%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-left: 30px;
}

 div.profileTitle {
    max-width: 591px;
    margin-left: 30px;
    

}
div.aboutMeContent div.descriptionContent {
    max-width:600px;
    display: flex;
    flex-direction: column;
    position: relative;
    top: -50px;


}
div.aboutMeContent div.descriptionContent img {
    width: 100px;
    height: 100px;
    position: relative;
    top: 40px

}


div.aboutMeContent div.descriptionContent span.descriptionText {
    color: #B3B3B3;
    text-align: justify;
    text-indent: 100px;
    font-family: 'Poppins';
    font-size: 16px;
    font-weight: 700;
}

div.profileBox div.profileDetails {
    background: linear-gradient(67deg, #FFF500 0%, rgba(118, 255, 173, 0.81) 72.95%, #508DE9 99.34%);
    padding: 43px;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
div.profileBox div.profileDetails div {
    display: flex ;
    align-items: flex-start ;
    
}

div.profileBox div.profileDetails div span {
    color: #FFF;
    
    font-family: Poppins;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;

}

div.profileBox div.profileDetails div img.iconProfileDetails {
    width: 26px;
    height: 26px;
    margin-right: 12px;
}
/* 
    Minhas Tecnologias  
*/
div.stackContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

div.profileBox div.stackTitleContainer {
    margin-top: 7rem;
    display: flex;

}
div.profileBox div.stackTitleContainer span.stackTitle {
    font-family: Poppins;
    font-size: 45px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    background: linear-gradient(88deg, #FFF500 0%, rgba(118, 255, 173, 0.81) 72.95%, #508DE9 99.34%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

div.profileBox div.stackTitleContainer img {
    width: 46px;
    height: 46px;
    margin-top: 10px;
    margin-right: 20px ;
}
div.gridStackContainer {
    display: flex;
    align-items: center;
    justify-content: center;
}


div.gridStackContainer ul {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 90%;
    margin: 40px 0;
    position: relative;
    list-style: none;

}

div.stackBox:hover {
    scale: 1;

}

div.stackBox {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 234px;
    height: 234px;
    border-radius: 20px;
    border: 3px solid rgba(252, 252, 252, 0.48);
    background: linear-gradient(44deg, #E4D443 0%, #76FFAD 51.04%, #508DE9 100%);
    scale: 0.9;
    transition: 0.3s all ease-in-out;


}
div.stackBox  img{
    width: 99px;
    height: 99px;
    
}
div.stackBox  span{
    color: #FFF;
    font-family: Poppins;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    padding-top: 5px;
    
}




@media screen and (max-width:1150px) {
    div.profileBox {
        flex-direction: column;
    }
    section.profileContainer div.profileBox div.profileBoxContainer {
        flex-direction: column;
    }
    section.profileContainer div.profileBox div.profileBoxContainer {
        gap: 30px
    }
    div.profileBox div.stackTitleContainer img {
        height: 30px;
        width: 30px;
    }
    div.profileBox div.stackTitleContainer span.stackTitle {
        font-size: 30px;
    }
    div.stackBox {
        height: 170px;
        width: 170px;
    }
    div.stackBox  img{
        width: 70px;
        height: 70px;
        
    }
    div.stackBox  span{
        font-size: 16px;
        
    }

}
@media screen and (max-width:641px) {
    div.stackBox {
        height: 140px;
        width: 140px;
    }
    div.stackBox  img{
        width: 50px;
        height: 50px;
    }
    div.stackBox  span{
        font-size: 12px;
        
    }
    div.profileBox div.stackTitleContainer span.stackTitle {
        font-size: 20px;
    }
    div.profileBox div.stackTitleContainer img{
        height: 20px;
        width: 20px;
        margin-top: 5px;
    }
    div.profileBox div.stackTitleContainer  {
        display: flex;
    }


    section.profileContainer {
        padding: 30px;
        padding-bottom: 0px;
    }
    div.aboutMeContent span.profileTitleText {
        font-size: 30px;
        margin-left: 20px;


    }
    div.aboutMeContent div.profileTitle img {
        width: 29px;
        height: 29px;
    }
    div.aboutMeContent div.profileTitle {
        display: flex;
        align-items: center;
        margin-left: 20px;
    }
    div.aboutMeContent div.descriptionContent span.descriptionText {
        font-size: 14px;
        text-align: justify;
        text-indent: 80px;
    }
    div.aboutMeContent div.descriptionContent img {
        width: 79px;
        height: 79px;
        position: relative;
        top: 30px

    }
    div.profileBox {
        position: relative;
        padding: 40PX;
       
    }
    div.aboutMeContent div.descriptionContent  {
       display: flex;
       flex-direction: column;
        position: relative;
        top: -20px;
      
    }
    div.profileBox div.profileDetails {
        padding: 30px;
    }
    div.profileBox div.profileDetails div span {
        font-size: 12px;
    }
    div.gridStackContainer ul {
        width: 100%;
    }

}
@media screen and (max-width:401px) {
    div.stackBox {
        height: 100px;
        width: 100px;
    }
    div.stackBox  img{
        width: 30px;
        height: 30px;
        
    }
    div.profileBox div.stackTitleContainer span.stackTitle {
        font-size: 16px;
    }
    div.profileBox div.stackTitleContainer img {
        height: 16px;
        width: 16px;
    }
    div.aboutMeContent span.profileTitleText {
        font-size: 20px;
    }
    div.aboutMeContent div.profileTitle img {
        width: 20px;
        height: 20px;
    }
    div.aboutMeContent div.descriptionContent img {
        height: 60px;
        width: 60px;
    }
    div.aboutMeContent div.descriptionContent span.descriptionText {
        text-indent: 60px;
        font-size: 12px;
    }
    div.profileBox div.profileDetails {
        padding: 20px
    }
    div.profileBox div.profileDetails div img.iconProfileDetails {
        font-size: 12px;
        height: 20px;
        width: 20px;
    }



}