section.footerContainer {
    width: 100%;
    height: 167px;
    border: 3px solid rgba(79, 79, 79, 0.65);
    background: rgba(25, 26, 37, 0.48);
    display: flex;
    justify-content: center;
    align-items: center;
}

div.footerDetails span {
    color: #FFF;
    font-family: 'Poppins';
    font-size: 22px;
    font-style: normal;
    font-weight: 700;
}

@media screen and (max-width:829px) {
    section.footerContainer {
        height: 100px;

    }
    div.footerDetails span{
    font-size: 12px;

    }
}
@media screen and (max-width:454px) {
    section.footerContainer {
        height: 80px;

    }
    div.footerDetails span{
    font-size: 8px;

    }
}